import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
export default {
	data() {
		return {
			user_info: '',
			categ:[],
			server_name: '请选择服务类型',
			server_id: 0,
			img_list: [
				// 'https://www.lianhexiu.com.cn/uploads/order/20210308/4c5d09b1b9657fad7e25c4f294699084.jpg',
				// 'https://www.lianhexiu.com.cn/uploads/order/20210308/4c5d09b1b9657fad7e25c4f294699084.jpg',
				// 'https://www.lianhexiu.com.cn/uploads/order/20210308/4c5d09b1b9657fad7e25c4f294699084.jpg'
			], //照片数组
			img_num: 0, //照片的数量
			multiArray: [
				[],
				[]
			], //上门时间数组
			multiIndex: [0, 0],
			show_categ:false,
			prohibit: false,
		}
	},
	onLoad(options) {
		
		//从0天开始循环出之后7天
		for (let i = 0; i < 7; i++) {
			this.getDay(i);
		}
		this.getHours();
		
	},
	onShow() {
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			this.categList();
		} else {
			wx.reLaunch({
				url: '/pages/auth/index',
			})
		}
	},
	methods: {
		categList(){
			this.request({
				url: interfaces.categList,
				data:{
					id:this.user_info.store_id
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						this.categ=res.data;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},
		getDay(day) {
			var today = new Date();
			var targetday_milliseconds = today.getTime() + 1000 * 60 * 60 * 24 * day;
			today.setTime(targetday_milliseconds); //注意，这行是关键代码
			var tYear = today.getFullYear();
			var tMonth = today.getMonth();
			var tDate = today.getDate();
			tMonth = this.doHandleMonth(tMonth + 1);
			tDate = this.doHandleMonth(tDate);

			this.multiArray[0].push(tYear + "-" + tMonth + "-" + tDate);
		},
		getHours() {
			var today = new Date();
			var tHours = today.getHours();
			for (let i = tHours; i <= 22; i = i + 2) {
				this.multiArray[1].push(i + ":00~" + (i + 2) + ":00");
			}
		},
		//当长度为1时，前面加个0，例如 7月改称07月
		doHandleMonth(month) {
			var m = month;
			if (month.toString().length == 1) {
				m = "0" + month;
			}
			return m;
		},

		bindMultiPickerChange(e) {
			//console.log(e);
			this.multiIndex=e.detail.value;
		},

		
		//参数一 e 数据源
		//参数二 t 成功success的数量
		//参数三 a 失败fail的数量
		//参数四 i 完成comlete的数量
		//参数五 n 要上传的图片数量
		uploadDIY: function(e, t, a, i, n) {
			var o = this,
				s = this;
			wx.uploadFile({
				url: interfaces.orderUpload,
				filePath: e[i],
				name: "file",
				header: {
					'content-type': 'application/json',
					token:s.token
				},
				success: function(e) {
					e.data=JSON.parse(e.data);
					var num = s.img_list.length + 1;
					t++;
					s.img_list.push(e.data);
					s.img_num = num;
				},
				fail: function(e) {
					a++;
				},
				complete: function() {
					++i == n ? wx.hideLoading() : o.uploadDIY(e, t, a, i, n);
				}
			});
		},
		
		choosePic() {
			var a = this;
			wx.chooseImage({
				count: 6,
				sizeType: ["compressed"],
				sourceType: ["album", "camera"],
				success: function(e) {
					e.tempFilePaths;
					var t = e.tempFilePaths.length;
					a.uploadDIY(e.tempFilePaths, 0, 0, 0, t);
				}
			});
		},

		//表单提交事件
		formSubmit(e) {
			var data = e.detail.value;
			
			data.server_id = this.server_id;
			data.server_name=this.server_name;
			if (data.server_id == 0) {
				wx.showToast({
					title: '请选择服务项目',
					icon: "none",
				});
				return;
			}
			if (common.isBlank(data.remark)) {
				wx.showToast({
					title: '请描述报修故障及内容',
					icon: "none",
				});
				return;
			}
			
			data.customer_time = this.multiArray[0][this.multiIndex[0]] + " " + this.multiArray[1][this.multiIndex[1]];
			data.pics = this.img_list.join();
			this.prohibit= true;
			console.log(data);
			this.request({
				url: interfaces.orderAdd,
				data: data,
				header: {
					token:this.user_info.token
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						wx.reLaunch({
							url: 'index',
						})
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},

		//点击报修图片预览图片事件
		preImage(url) {
			wx.previewImage({
				current: url,
				urls: this.img_list,
			})
		},

		//删除订单图片
		picsDelete(index) {
			this.img_list.splice(index, 1);
			this.img_num--;
		},
		//显示选择服务项目页面
		selectCateg() {
			this.show_categ=true;
		},
		//选择服务项目
		checkCateg(server_name,server_id){
			this.server_name = server_name;
			this.server_id = server_id;
			this.show_categ=false;
		},
	}
}
